import usePositionListAction from '@/hooks/position/usePositionListAction'
import usePositionStore from '@/store/position'
import { TableSortTh } from '@cetus/design'
import useTokenPriceStore from '@cetus/stores/src/tokenPrice'
import { d, isAvailableObject } from '@cetus/utils'
import { HStack, Image, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import PositionItemContent from './PositionItemContent'

const itemWidth = ['480px', '20%', '20%', '20%', '20%']

const SORT_BY_LIST = [
  { label: 'APR', value: 'apr' },
  { label: 'Liquidity', value: 'liquidity' },
  { label: 'Pending Fees', value: 'fees' },
  { label: 'Pending Rewards', value: 'rewards' }
]

const SORT_BY_OBJECT = SORT_BY_LIST.reduce((obj: any, item) => {
  obj[item.value] = item
  return obj
}, {})

function PositionItem({ poolInfo, priceDirect }: { poolInfo: any; priceDirect?: boolean }) {
  const { coinPriceObj } = useTokenPriceStore()
  const { posApiPoolData } = usePositionStore()
  const { getPositionSortList } = usePositionListAction()

  const [sortRule, setSortRule] = useState<'asc' | 'desc'>('desc')
  const [sortBy, setSortBy] = useState<{ label: string; value: string }>({ label: '', value: '' })

  const handleClickSort = (item: any) => {
    if (item?.value === sortBy.value) {
      setSortRule(prev => (prev === 'desc' ? 'asc' : 'desc'))
    } else {
      setSortRule('desc')
      setSortBy(item)
    }
  }
  const [sortList, setSortList] = useState([])
  const positionList = useMemo(() => {
    if (!isAvailableObject(poolInfo)) return []
    const sortedList: any = getPositionSortList(sortBy.value, sortRule, poolInfo.list)
    const list = sortBy.value ? sortedList : poolInfo.list

    console.log('üöÄ ~ positionList ~ sortedList:', list, sortedList)
    setSortList(sortedList)
    return list
  }, [sortBy.value, sortRule, poolInfo?.list, coinPriceObj])

  const hasPositiveAmount = (key: 'totalMining' | 'totalFarming') => sortList.some((item: any) => d(item[key]).gt(0))

  const showMiningIcon = useMemo(() => hasPositiveAmount('totalMining'), [sortList])
  const showFarmingIcon = useMemo(() => hasPositiveAmount('totalFarming'), [sortList])

  return (
    <VStack gap="12px" w="100%">
      <HStack w="100%" p="0 16px">
        <Text w={itemWidth[0]}>Price Range</Text>
        {SORT_BY_LIST.map((item, idx) => {
          if (item.value !== 'rewards' || showMiningIcon || showFarmingIcon) {
            return (
              <HStack key={item.value} gap="4px" w={itemWidth[idx + 1]} justify="flex-end">
                {showMiningIcon && item.value === 'rewards' && <Image w="18px" h="18px" src="/images/icon_mining.png" />}
                {showFarmingIcon && item.value === 'rewards' && <Image w="18px" h="18px" src="/images/icon_farms.png" />}
                <TableSortTh labelInfo={SORT_BY_OBJECT[item.value]} sortRule={sortRule} sortBy={sortBy} clickSort={handleClickSort} />
              </HStack>
            )
          }
          return null
        })}
      </HStack>
      <VStack w="100%" maxH="440px" overflow="auto" gap="12px">
        {positionList.map((item: any) => (
          <PositionItemContent
            key={item?.id}
            positionInfo={item}
            positionItemWidth={itemWidth}
            priceDirect={priceDirect}
            showMiningIcon={showMiningIcon}
            showFarmingIcon={showFarmingIcon}
          />
        ))}
      </VStack>
    </VStack>
  )
}

export default PositionItem
